import { extname } from 'src/app/shared/models/paths.model';
import { isRoot } from '../../pages/editor/shared/models/filters';
import { IResource, ResourceTypes } from '../../pages/editor/shared/models/resource';
import { fileIcons } from './theme.files';
import { folderIcons } from './theme.folders';

const BASE_DIR = 'static/editor/assets/icons/explorer/';

export function iconPath(input: IResource) {
    let path = '';
    if (input) {
        if (input.type === ResourceTypes.Folder) {
            const theme = folderIcons[0];
            path = BASE_DIR + theme.defaultIcon.name;
            if (isRoot(input)) {
                path = BASE_DIR + theme.rootFolder.name;
            } else {
                const icon = theme.icons.find(item =>
                    item.folderNames.includes(input.name.toLowerCase())
                );
                if (icon) {
                    path = BASE_DIR + icon.name;
                }
            }
            if (input.expanded) {
                path += '-open';
            }
            path += '.svg';
        } else {
            const extension = extname(input.path);
            const icon =
                fileIcons.icons.find(item => {
                    return (item.fileExtensions || []).includes(extension);
                }) || fileIcons.defaultIcon;
            path = BASE_DIR + icon.name + '.svg';
        }
        return path;
    }
    return '';
}
